.GroupItem{
    width: 100%;
    height: 85px;/* breakpoint changeable */
    display: flex;
    align-items: center;
    color:#ddd;

    padding: 10px 20px; /* breakpoint changeable */
    margin-bottom: 0px; /* breakpoint changeable */
    
}

.GroupItem-img{
    background-color: var(--color-primary-hover);
    width: 25%; /* breakpoint changeable */
    height: 100%;
    border-radius: 10px;
}

.GroupItem-img img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    
}

.GroupItem-info{
    min-width: 0;
    width: 70%;
    height: 100%;
    display: block flex;
    flex-direction: column;
    
    
    padding: 5px 0 2px 10px; /* breakpoint changeable */
    padding-right: 30px; /* breakpoint changeable */
}

.info-title{
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.GroupItem-icon{
    background-color: var(--color-primary-hover);
    width: 10%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 5px 0; /* breakpoint changeable */
    border-radius: 30px;
}


