/* desk */
@media only screen and (min-width: 1024px) {
    .GroupItem{
        width: 325px;
        height: 300px;/* breakpoint changeable */
        display: flex;
        flex-direction: column;
    
        padding: 10px 20px; /* breakpoint changeable */
        margin-bottom: 30px; /* breakpoint changeable */

        transition: .5s;
        
    }

    .GroupItem:hover{
        cursor: pointer;
        transform: translateY(-15px);
        transition: .5s;
    }
    
    .GroupItem-img{
        background-color: var(--color-primary-hover);
        width: 100%;
        height: 70%;
        border-radius: 15px;
    }
    
    .GroupItem-img img {
        width: 100%;
        height: 100%;
        border-radius: 15px;
        
    }
    
    .GroupItem-info{
        background-color: #fff;
        min-width: 0;
        width: 100%;
        height: 30%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: #000;
        
        
        padding: 10px 15px; /* breakpoint changeable */
        border-radius: 0 0 15px 15px;
    }
    
    .info-title{
        width: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis ;
    }
    
    .GroupItem-icon{
        display: none;
    }

}

